import {useLocation, useNavigate} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {HeroData} from "../types";
import React, {useEffect, useState} from "react";
import {HeaderBlock} from "./HeaderBlock";
import {Box, Button, Divider} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {SelectedHeroPack} from "./SelectedHeroPack";
import {HeroModernGrid} from "./HeroModernGrid";
import {MySettings} from "../types/MySettings";
import fetchRetry from "fetch-retry-ts";

export interface IProps {
    openMenuFunction: any,
    mainURL:string
}

interface ITwoHeroPacks {
    HPA: IHeroPack,
    HPD: IHeroPack,
    fight_id: number,
    ts_created: number,
    r: number,
    bseed: number,
    istimeup: number,
    bduration: number
}

export function FightsResult(props: IProps) {
    const newFetch = fetchRetry(fetch, {
        retries: 5,
        retryDelay: 800
    });
    const navigate = useNavigate();
    const location = useLocation();
    const { t } = useTranslation();
    const templateHeroPackA = HeroData.getEmptyHeroPack()
    const templateHeroPackD = HeroData.getEmptyHeroPack()
    const [headerMessage, setHeaderMessage] = useState(t('Choose5Heroes').toString());
    const queryParams: any = new URLSearchParams(location?.search || "")
    const [memoArray,] = useState({requestsInProgress:0})
    const [isInitialized, setIsInitialized] = useState(false)
    if (queryParams.get("hidsA")) {
        const x = queryParams.get("hidsA")?.split(",")
        if (x && x.length <= 5) {
            for (const z of x) {
                templateHeroPackA.hIds.push(~~z)
            }
        }
    }
    if (queryParams.get("hidsD")) {
        const x = queryParams.get("hidsD")?.split(",")
        if (x && x.length <= 5) {
            for (const z of x) {
                templateHeroPackD.hIds.push(~~z)
            }
        }
    }
    const [openedMyPacksMenuA,setOpenedMyPacksMenuA] = useState(templateHeroPackA.hIds.length!==5)
    const [openedMyPacksMenuD,setOpenedMyPacksMenuD] = useState(templateHeroPackD.hIds.length!==5)
    const [heroPackA, setHeroPackA] = useState(templateHeroPackA);
    const [heroPackD, setHeroPackD] = useState(templateHeroPackD);
    const [heroPacks, setHeroPacks] = useState([] as ITwoHeroPacks[]);
    const [requestsInProgress, setRequestsInProgress] = useState(0)

    useEffect(() => {
        console.log("Initialize effect heroPackA="+heroPackA.hIds.join(",")+" heroPackD="+heroPackD.hIds.join(","))
        if (!isInitialized && heroPackA.hIds.length === 5 && heroPackD.hIds.length === 5) {
            setIsInitialized(true)
            updateHeroPackInfo(heroPackA,heroPackD)
        }

    });

    useEffect(()=> {
        console.log("Changed effect heroPackA="+heroPackA.hIds.join(",")+" heroPackD="+heroPackD.hIds.join(","))
        navigate(props.mainURL+"?hidsA=" + heroPackA.hIds.join(",")+"&hidsD=" + heroPackD.hIds.join(","))
        if (heroPackA.hIds.length===5 && heroPackD.hIds.length===5) {
            setHeroPacks([])
            updateHeroPackInfo(heroPackA,heroPackD)
        } else {
            setHeaderMessage(t('Choose5Heroes'))
            if (heroPackA.hIds.length!==5) {
                setOpenedMyPacksMenuA(true)
            }
            if (heroPackD.hIds.length!==5) {
                setOpenedMyPacksMenuD(true)
            }
        }
    },[heroPackA,heroPackD])

    const updateAvailableHeroes = function (pHeroPackA: IHeroPack,pHeroPackD: IHeroPack) {
        if (pHeroPackA.hIds.length === 5 && pHeroPackD.hIds.length ===5) {
            const HPA=[...pHeroPackA.hIds]
            const HPD=[...pHeroPackD.hIds]
            HPA.sort((a,b)=>{return a-b})
            HPD.sort((a,b)=>{return a-b})
            let url = MySettings.apiUrl+"fightstat/"+HPA.join("/")+"/"+HPD.join("/")
            changeHeaderMessage(t('LoadingPacksFromStat'))
            memoArray.requestsInProgress++
            setRequestsInProgress(memoArray.requestsInProgress)
            newFetch(url, {
                method: "GET"
            }).then(res => res.json())
                .then(
                    (result) => {
                        let nTotalFound = 0
                        if (result['items']) {
                            const heroLostPacks = []
                            for (const x in result['items']) {
                                nTotalFound++
                                const z = result['items'][x]
                                heroLostPacks.push( {HPA: {
                                    hIds: [~~z.a1, ~~z.a2, ~~z.a3, ~~z.a4, ~~z.a5],
                                    extendedInfo: {
                                        statusInfo: "",
                                        h1h: z.a1h,
                                        h2h: z.a2h,
                                        h3h: z.a3h,
                                        h4h: z.a4h,
                                        h5h: z.a5h,
                                        h1dd: z.a1dd,
                                        h2dd: z.a2dd,
                                        h3dd: z.a3dd,
                                        h4dd: z.a4dd,
                                        h5dd: z.a5dd,
                                        h1dr: z.a1dr,
                                        h2dr: z.a2dr,
                                        h3dr: z.a3dr,
                                        h4dr: z.a4dr,
                                        h5dr: z.a5dr,
                                        h1dh: z.a1dh,
                                        h2dh: z.a2dh,
                                        h3dh: z.a3dh,
                                        h4dh: z.a4dh,
                                        h5dh: z.a5dh,
                                    }
                                },HPD: {
                                    hIds: [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5],
                                        extendedInfo: {
                                        statusInfo: "",
                                            h1h: z.d1h,
                                            h2h: z.d2h,
                                            h3h: z.d3h,
                                            h4h: z.d4h,
                                            h5h: z.d5h,
                                            h1dd: z.d1dd,
                                            h2dd: z.d2dd,
                                            h3dd: z.d3dd,
                                            h4dd: z.d4dd,
                                            h5dd: z.d5dd,
                                            h1dr: z.d1dr,
                                            h2dr: z.d2dr,
                                            h3dr: z.d3dr,
                                            h4dr: z.d4dr,
                                            h5dr: z.d5dr,
                                            h1dh: z.d1dh,
                                            h2dh: z.d2dh,
                                            h3dh: z.d3dh,
                                            h4dh: z.d4dh,
                                            h5dh: z.d5dh,
                                    }
                                },
                                    fight_id: z.fight_id,
                                    ts_created: z.ts_created,
                                    r: z.r,
                                    bseed: z.bseed,
                                    istimeup: z.istimeup,
                                    bduration: z.bduration
                                })
                            }
                            setHeroPacks(heroLostPacks)
                        }
                        changeHeaderMessage(t('FoundXPacks',{x:nTotalFound}))//" " + nTotalFound + " -")
                    }).finally(() => {
                memoArray.requestsInProgress--
                setRequestsInProgress(memoArray.requestsInProgress)
            })
        }
    }
    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }

    const updateHeroPackInfo = function (pHeroPackA: IHeroPack,pHeroPackD: IHeroPack) {
        updateAvailableHeroes(pHeroPackA,pHeroPackD)
    }

    const toggleMyPacksMenuA=function () {
        setOpenedMyPacksMenuA(!openedMyPacksMenuA)
    }
    const toggleMyPacksMenuD=function () {
        setOpenedMyPacksMenuD(!openedMyPacksMenuD)
    }
    const heroSelect = function(heroId: number,pHeroPack:IHeroPack,pSetHeroPackFunction:any) {
        if (HeroData.get_instance().allHeroesIds.includes(heroId)) {
            const newHeroPack={hIds:pHeroPack.hIds,extendedInfo:{statusInfo: ""}}
            const index = newHeroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (newHeroPack.hIds.length < 5) {
                    newHeroPack.hIds.push(heroId)
                    pSetHeroPackFunction(newHeroPack)
                }
            } else {
                newHeroPack.hIds.splice(index, 1);
                pSetHeroPackFunction(newHeroPack)
            }
        }
    }

    const onHeroSelectA = function (heroId: number) {
        heroSelect(heroId,heroPackA,setHeroPackA)
    }
    const onHeroSelectD = function (heroId: number) {
        heroSelect(heroId,heroPackD,setHeroPackD)
    }
    const finishEditA = function() {
        setOpenedMyPacksMenuA(false)
       // navigate(props.mainURL+"?hidsA=" + heroPackA.hIds.join(",")+"&hidsD=" + heroPackD.hIds.join(","))
    }
    const finishEditD = function() {
        setOpenedMyPacksMenuD(false)
      //  navigate(props.mainURL+"?hidsA=" + heroPackA.hIds.join(",")+"&hidsD=" + heroPackD.hIds.join(","))
    }
    const renderHeroPacks = function () {
        const items = []
        const emptyMap = new Map<number, number>()
        for (const i in heroPacks) {
            let status=t(heroPacks[i].r===1?'Won':'Lost')
            status+=heroPacks[i].istimeup===0?','+t('TimeUp'):''
            status+=~~heroPacks[i].bduration>0?','+t('duration')+' '+~~heroPacks[i].bduration+t('secs'):''
            status+=', '+t('tested at')+' '+(new Date(heroPacks[i].ts_created*1000)).toLocaleDateString()+' '+(new Date(heroPacks[i].ts_created*1000)).toLocaleTimeString()
            items.push(
            <Box key={heroPacks[i].fight_id} className={"fightResultBox"}>
                <Box sx={{whiteSpace: 'normal', maxWidth: '90vw'}}>{status}</Box>
                <SelectedHeroPack additionalClasses={[]} heroPack={heroPacks[i].HPA}
                                             heroProbs={emptyMap}
                                             onHeroSelect={()=>{}} selectedHeroes={[]}
                                             key={"A"+heroPacks[i].fight_id}/>
                <SelectedHeroPack additionalClasses={[]} heroPack={heroPacks[i].HPD}
                                             heroProbs={emptyMap}
                                             onHeroSelect={()=>{}} selectedHeroes={[]}
                                             key={"D"+heroPacks[i].fight_id}/>
            </Box>)
        }
        return items
    }

    return (
        <Box className={"counterPackScreen"}>
            <HeaderBlock openMenuFunction={props.openMenuFunction}
                         message={headerMessage}/>
            <Divider>{t('ChosenTeamAttack')}
                {(!openedMyPacksMenuA) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} onClick={toggleMyPacksMenuA}>{t("Edit")}</Button>)}
                {(openedMyPacksMenuA && heroPackA.hIds.length===5) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} onClick={finishEditA}>{t("Done")}</Button>)}
            </Divider>
            <SelectedHeroPack heroPack={heroPackA} heroProbs={HeroData.emptyHeroProbMap}
                              onHeroSelect={onHeroSelectA} selectedHeroes={[]} additionalClasses={[]}/>

            {(openedMyPacksMenuA) && (
                <React.Fragment>
                    {(heroPackA.hIds.length<5) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} variant={"outlined"}>{t("...")}</Button>)}

                    <HeroModernGrid additionalClasses={[]}  onClick={onHeroSelectA}
                                    selectedHeroes={heroPackA.hIds} highlightedHeroes={[]}/>
                </React.Fragment>
            )}
            <Divider>{t('ChosenTeamDefence')}
                {(!openedMyPacksMenuD) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} onClick={toggleMyPacksMenuD}>{t("Edit")}</Button>)}
                {(openedMyPacksMenuD && heroPackD.hIds.length===5) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} onClick={finishEditD}>{t("Done")}</Button>)}
            </Divider>
            <SelectedHeroPack heroPack={heroPackD} heroProbs={HeroData.emptyHeroProbMap}
                              onHeroSelect={onHeroSelectD} selectedHeroes={[]} additionalClasses={[]}/>

            {(openedMyPacksMenuD) && (
                <React.Fragment>
                    {(heroPackD.hIds.length<5) && (<Button sx={{bgcolor: 'primary.light',marginLeft:'2em'}} className={"myButton"} variant={"outlined"}>{t("...")}</Button>)}

                    <HeroModernGrid additionalClasses={[]}  onClick={onHeroSelectD}
                                    selectedHeroes={heroPackD.hIds} highlightedHeroes={[]}/>
                </React.Fragment>
            )}
            {(heroPacks.length>0) && (
                <Box className={"counterPackResultPacks"}>
                    {renderHeroPacks()}
                </Box>
            )}
        </Box>
    )
}