import {useTranslation} from "react-i18next";
import React, {useState} from "react";
import HeroFightQueueMode from "../components/heroFightQueueMode";
import {HeaderBlock} from "./HeaderBlock";
import {Box} from "@mui/material";

export interface IProps {
    openMenuFunction: any,
}

export function QueueScreen(props: IProps) {
    const { t } = useTranslation();
    const [headerMessage, setHeaderMessage] = useState(t('Queue').toString());

    return (
        <Box className={"counterPackScreen"}>
            <HeaderBlock openMenuFunction={props.openMenuFunction}
                         message={headerMessage}/>
            <HeroFightQueueMode queueItems={[]}/>
        </Box>
    )
}