import React, {useState} from "react";
import {Box, Menu, MenuItem, Tooltip} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "./HeroModernIcon";
import {HeroData} from "../types";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import LinkIcon from '@mui/icons-material/Link';
import CancelRoundedIcon from '@mui/icons-material/CancelRounded';
import {useTranslation} from "react-i18next";
import {useNavigate} from "react-router-dom";
import StarIcon from '@mui/icons-material/Star';

interface IProps {
    heroPack: IHeroPack,
    heroProbs: Map<number, number>,
    onHeroSelect: any,
    selectedHeroes: number[],
    additionalClasses: string[],
    additionalIconClasses?: string[],
    currentPack?: IHeroPack,
    currentInAttack?: boolean,
    onRemoveFunction?: any
}

export function SelectedHeroPack(props: IProps) {
    const navigate = useNavigate();
    const [anchorEl, setAnchorEl] = useState(null)
    const {t} = useTranslation();
    const [isInMyPacks,setIsInMyPacks] = useState(!!HeroData.isInMyPacks(props.heroPack))
    const renderHeroIcons = function () {
        /* props.heroPack.hIds.sort((a, b) => {
             return (HeroData.getHeroById(a)?.battleOrder || 0) - (HeroData.getHeroById(b)?.battleOrder || 0)
         })*/
        const items: any = []
        for (let i = 1; i <= 5; i++) {
            const hId = props.heroPack.hIds[i - 1]
            const hp: any = props.heroPack.extendedInfo
            const additClasses=[...props.additionalIconClasses || []]
            additClasses.push((hId && props.selectedHeroes.includes(hId))?"highLighted":"")
            additClasses.push("selectedHeroPackIcons")
            items.push(<HeroModernIcon key={i} additionalClasses={additClasses} heroId={hId}
                                       heroProb={props.heroProbs?.get(hId)}
                                       onClick={props.onHeroSelect} order={hId?(HeroData.getHeroById(hId)?.battleOrder || 0):100000}
                                       health={hp['h' + i + 'h']} energy={hp['h' + i + 'e']}
                                       damageDealt={hp['h' + i + 'dd']} damageReceived={hp['h' + i + 'dr']}
                                       healed={hp['h' + i + 'dh']}/>)
        }
        return items
    }

    const copyLink = function () {
        navigator.clipboard.writeText(HeroData.translateHeroIdsArray(props.heroPack.hIds)).then(() => {
        })
    }

    const seeCounterPacks = function () {
        navigate("/counterPack?hids=" + props.heroPack.hIds.join(","))
    }
    const seeAttackPacks = function () {
        navigate("/attackPack?hids=" + props.heroPack.hIds.join(","))
    }
    const addToMyPacks = function () {
        HeroData.addToMyPacksStore(props.heroPack);
        setIsInMyPacks(HeroData.isInMyPacks(props.heroPack))
        setAnchorEl(null)
    }

    const removeFromMyPacks = function () {
        HeroData.removeFromMyPacksStore(props.heroPack);
        setIsInMyPacks(HeroData.isInMyPacks(props.heroPack))
    }

    const handleMenuClick = function (e: any) {
        setAnchorEl(e.currentTarget)
    }

    const handleMenuClose = function () {
        setAnchorEl(null)
    }

    const renderMenuItems = function () {
        const menuItems: JSX.Element[] = []
        let key = 0
        menuItems.push(<MenuItem key={key++} onClick={seeCounterPacks}>{t('Find counter teams')}</MenuItem>)
        menuItems.push(<MenuItem key={key++} onClick={seeAttackPacks}>{t('Find who lose in defence')}</MenuItem>)
        if (!isInMyPacks) menuItems.push(<MenuItem key={key++} onClick={addToMyPacks}>{t('Add to favorite teams')}</MenuItem>);
        /*if (props.heroIdsA !== undefined && props.heroIdsD !== undefined) menuItems.push(<MenuItem key={key++} onClick={seeTrainMode}>Add to Train both packs</MenuItem>)*/
        return menuItems
    }

    const removeIconClick = function () {
        props.onRemoveFunction(props.heroPack)
    }

    const additionalClasses = "selectedHeroPack " + props.additionalClasses.join(" ")
    //<Tooltip title={t('CopyLinkToPack').toString()}><LinkIcon onClick={copyLink}/></Tooltip>
    return (
        <Box className={additionalClasses}>
            {props.heroPack?.extendedInfo?.cwr !== undefined && props.heroPack?.extendedInfo?.cc !== undefined &&
            (<Box className={"selectedHeroPackExtendedCurrent"}>
                <Tooltip title={"   "}><span><img alt={""}
                                                                            src={"./trophy_a_w.png"}/><span>{props.heroPack.extendedInfo.cwr + "%"}</span></span></Tooltip>
                <Tooltip title={"   "}><span><img alt={""}
                                                                               src={"./attack_w.png"}/><span>{props.heroPack.extendedInfo.cc}</span></span></Tooltip>
            </Box>)}
            <Box className={"selectedHeroPackIcons"}>
                {renderHeroIcons()}
                <Box className={"linkVerticalBar"} sx={{order: 10000000}}>
                    {(props.onRemoveFunction !== undefined) && (
                        <Tooltip title={t('Remove').toString()}><CancelRoundedIcon
                            onClick={removeIconClick}/></Tooltip>)}

                    <Box id="demo-positioned-button"
                         aria-controls="demo-positioned-menu"
                         aria-haspopup="true"
                         aria-expanded={anchorEl ? 'true' : undefined}
                         onClick={handleMenuClick}><Tooltip
                        title={t('AdditionalInfo').toString()}><MoreVertIcon/></Tooltip></Box>
                    { isInMyPacks && (
                        <StarIcon onClick={removeFromMyPacks}/>
                    )}
                </Box>
            </Box>
            <Menu
                id="demo-positioned-menu"
                aria-labelledby="demo-positioned-button"
                anchorEl={anchorEl}
                open={!!anchorEl}
                onClose={handleMenuClose}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                {renderMenuItems()}
            </Menu>
            {(HeroData.isNNAndNotNegative(props.heroPack?.extendedInfo?.wra) || HeroData.isNNAndNotNegative(props.heroPack?.extendedInfo?.ca) ||HeroData.isNNAndNotNegative(props.heroPack?.extendedInfo?.wrd) ||HeroData.isNNAndNotNegative(props.heroPack?.extendedInfo?.cd)) && (
            <Box className={"selectedHeroPackExtended"}>
                <span>{t('Total')}:</span>
                <Tooltip title={" %   "}><span><img alt={""}
                                                                    src={"./trophy_a_w.png"}/><span>{HeroData.nvl(props.heroPack?.extendedInfo?.wra, "?") + "%"}</span></span></Tooltip>
                <Tooltip title={"   "}><span><img alt={""}
                                                                  src={"./attack_w.png"}/><span>{HeroData.nvl(props.heroPack?.extendedInfo?.ca, "?")}</span></span></Tooltip>
                <Tooltip title={" %   "}><span><img alt={""}
                                                                     src={"./trophy_d_w.png"}/><span>{HeroData.nvl(props.heroPack?.extendedInfo?.wrd, "?") + "%"}</span></span></Tooltip>
                <Tooltip title={"   "}><span><img alt={""}
                                                                   src={"./shield_w.png"}/><span>{HeroData.nvl(props.heroPack?.extendedInfo?.cd, "?")}</span></span></Tooltip>
            </Box>)}
            {(HeroData.isNN(props.heroPack?.extendedInfo?.hl) || HeroData.isNN(props.heroPack?.extendedInfo?.hd) || HeroData.isNN(props.heroPack?.extendedInfo?.hf) || HeroData.isNN(props.heroPack?.extendedInfo?.hw) || HeroData.isNN(props.heroPack?.extendedInfo?.ha) || HeroData.isNN(props.heroPack?.extendedInfo?.he)) && (
                <React.Fragment>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>:</span>
                        <Tooltip title={"  "}><span><img alt={""}
                                                                      src={"./hdarkness.png"}/><img alt={""}
                                                                                                    src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.hd)}</span>
                        <Tooltip title={"  "}><span><img alt={""}
                                                                       src={"./hlight.png"}/><img alt={""}
                                                                                                  src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.hl)}</span>
                    </Box>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>:</span>
                        <Tooltip title={"  "}><span><img alt={""}
                                                                      src={"./hwater.png"}/><img alt={""}
                                                                                                 src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.hw)}</span>
                        <Tooltip title={"  "}><span><img alt={""}
                                                                       src={"./hwind.png"}/><img alt={""}
                                                                                                 src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.ha)}</span>
                    </Box>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>:</span>
                        <Tooltip title={"  "}><span><img alt={""} src={"./hearth.png"}/><img alt={""}
                                                                                                           src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.he)}</span>
                        <Tooltip title={"  "}><span><img alt={""} src={"./hfire.png"}/><img alt={""}
                                                                                                         src={"./damage.png"}/></span></Tooltip>
                        <span>{HeroData.formatK(props.heroPack?.extendedInfo?.hf)}</span>
                    </Box></React.Fragment>)}
        </Box>
    )
}