import {Box, Button, Chip, Divider} from "@mui/material";
import {useLocation, useNavigate} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {HeaderBlock} from "./HeaderBlock";
import React, {useEffect, useState} from "react";
import {HeroData} from "../types";
import DeleteIcon from "@mui/icons-material/Delete";
import HourglassBottomIcon from "@mui/icons-material/HourglassBottom";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import {CounterPackFilterModal} from "./CounterPackFilterModal";
import {IHeroPack} from "../types/IHeroPack";
import {MySettings} from "../types/MySettings";
import {SelectedHeroPack} from "./SelectedHeroPack";


const myTopPacksOptions = {
    topAttack: {

    },
    topDefence: {

    },
    topHydra: {

    }
}

function constructHeroFilter(pHeroFilter: number[]) {
    let result = "%"
    pHeroFilter.sort((a,b) => {
        return a-b
    })
    for (let i = 0; i <= 5 && i < pHeroFilter.length; i++) {
        result += ':' + pHeroFilter[i] + ':%'
    }
    return escape(result)
}

interface IProps {
    gMode: string,
    mainURL:string,
    openMenuFunction: any,
}

export function TopPacksScreen(props:IProps) {
    const isHydra=props.gMode!=='a' && props.gMode!=='d'
    const navigate = useNavigate();
    const location = useLocation();
    const { t, i18n } = useTranslation();
    const [headerMessage, setHeaderMessage] = useState(t('Loading').toString());
    const params: any = new URLSearchParams(location?.search || "")

    let pMinFights = 0;
    if (params.get("minFightsCount")) {
        const x = ~~params.get("minFightsCount")
        if (x >= 0) {
            pMinFights = x
        }
    }
    const pHeroFilterInclude = []
    if (params.get("heroFilterInclude")) {
        const x = params.get("heroFilterInclude")?.split(",") || []
        for (let i = 0; i < 5 && i < x.length; i++) {
            if (HeroData.getHeroById(~~x[i])) {
                pHeroFilterInclude.push(~~x[i])
            }
        }
    }
    const pHeroFilterExclude = []
    if (params.get("heroFilterExclude")) {
        const x = params.get("heroFilterExclude")?.split(",") || []
        for (let i = 0;  i < x.length; i++) {
            if (HeroData.getHeroById(~~x[i])) {
                pHeroFilterExclude.push(~~x[i])
            }
        }
    }
    let pMinWinRate = 0
    if (params.get("minWinRate")) {
        const x = ~~params.get("minWinRate")
        if (x >= 0 && x <= 100) {
            pMinWinRate = x
        }
    }


    const [filterOpened, setFilterOpened] = useState(false)
    const [isInitialized, setIsInitialized] = useState(false)
    const [isUpdateOccurred, setIsUpdateOccurred] = useState(false)
    const [minWinRate, setMinWinRate] = useState(pMinWinRate)
    const [minFights, setMinFights] = useState(pMinFights)
    const [heroFilterInclude, setHeroFilterInclude] = useState(pHeroFilterInclude)
    const [heroFilterExclude, setHeroFilterExclude] = useState(pHeroFilterExclude)
    const [requestsInProgress, setRequestsInProgress] = useState(0)
    const [heroPacks, setHeroPacks] = useState([] as IHeroPack[])
    const [, setTotalFound] = useState(0)
    const [memoArray, setMemoArray] = useState({
        minWinRate: minWinRate,
        minFights: minFights,
        heroFilterInclude: heroFilterInclude,
        heroFilterExclude: heroFilterExclude,
        filterMinFights: 0,
        filterMaxFights: 0,
        filterMinWinRate: 0,
        filterMaxWinRate: 0,
        requestsInProgress: 0
    })

    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }

    const updateHeroPackInfo = function (pMinWinRate: number, pMinFights: number, pHeroFilterInclude: number[], pHeroFilterExclude: number[]) {
            let url = MySettings.apiUrl+"topPacks3/"+props.gMode+"/"+HeroData.constructHeroFilterNew(pHeroFilterInclude)+"/10000%3A"+escape(pHeroFilterExclude.join(":"))+"/"+pMinWinRate+"/"+pMinFights
            changeHeaderMessage(t('LoadingPacksFromStat'))
            memoArray.requestsInProgress++
            setRequestsInProgress(memoArray.requestsInProgress)
            fetch(url, {
                method: "GET"
            }).then(res => res.json())
                .then(
                    (result) => {
                        let nTotalFound = 0
                        if (result['items']) {
                            const heroLostPacks = []


                            for (const x in result['items']) {
                                const z = result['items'][x]
                                let lostPack = []
                                lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]

                                //nTotalFound = z.tc
                                heroLostPacks.push({
                                    hIds: lostPack,
                                    extendedInfo: {
                                        statusInfo: "",
                                        wra: z.wra,
                                        wrd: z.wrd,
                                        ca: z.ca,
                                        cd: z.cd,
                                        wa: z.wa,
                                        wd: z.wd,
                                        hf: z.h0d,
                                        hw: z.h1d,
                                        he: z.h2d,
                                        ha: z.h3d,
                                        hl: z.h4d,
                                        hd: z.h5d
                                        //cc: z.cc,
                                        //cwr: z.cwr
                                    }
                                })
                            }

                            setHeroPacks(heroLostPacks)

                            setTotalFound(nTotalFound)

                        }
                        if (!isUpdateOccurred) setIsUpdateOccurred(true)
                        changeHeaderMessage (t('Loaded'))
                        const x=1
                    }).finally(() => {
                memoArray.requestsInProgress--
                setRequestsInProgress(memoArray.requestsInProgress)
            })

    }

    useEffect(() => {
        if (!isInitialized) {
            setIsInitialized(true)
            updateHeroPackInfo(minWinRate, minFights, heroFilterInclude, heroFilterExclude)
        }
    });

    const onFilterClick = function () {
        setMemoArray(
            {
                minWinRate: minWinRate,
                minFights: minFights,
                heroFilterInclude: heroFilterInclude,
                heroFilterExclude: heroFilterExclude,
                filterMinFights: 0,
                filterMaxFights: isHydra?10:1000,
                filterMinWinRate: 0,
                filterMaxWinRate: isHydra?100000000:100,
                requestsInProgress: memoArray.requestsInProgress
            }
        )
        setFilterOpened(true)
    }

    const navigateTo = function (pMinFights:number | undefined,pMinWinRate:number | undefined,pHeroFilterInclude:number[] | undefined,pHeroFilterExclude:number[] | undefined) {
        let newURL=props.mainURL
        newURL+="?minFightsCount="+(pMinFights!==undefined?pMinFights:minFights)
        newURL+="&minWinRate="+(pMinWinRate!==undefined?pMinWinRate:minWinRate)
        newURL+="&heroFilterInclude="+(pHeroFilterInclude!==undefined?pHeroFilterInclude.join(","):heroFilterInclude.join(","))
        newURL+="&heroFilterExclude="+(pHeroFilterExclude!==undefined?pHeroFilterExclude.join(","):heroFilterExclude.join(","))
        console.log("Navigate to "+newURL)
        navigate(newURL)
    }
    const handleModalClose = function (newMemoArray: any) {
        setMinWinRate(newMemoArray.minWinRate)
        setMinFights(newMemoArray.minFights)
        const newHeroFilterInclude = [...newMemoArray.heroFilterInclude]
        const newHeroFilterExclude = [...newMemoArray.heroFilterExclude]
        setHeroFilterInclude(newHeroFilterInclude)
        setHeroFilterExclude(newHeroFilterExclude)
        setMemoArray(newMemoArray)
        setFilterOpened(false)
        navigateTo(newMemoArray.minFights,newMemoArray.minWinRate,newHeroFilterInclude,newHeroFilterExclude)
        updateHeroPackInfo(newMemoArray.minWinRate, newMemoArray.minFights, newHeroFilterInclude, newHeroFilterExclude)
    }
    const handleDeleteFilterChip = function (e: any) {
        let filterType = undefined
        let heroId = undefined
        if (e?.target?.parentNode?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.parentNode?.dataset?.filterheroid
        } else if (e?.target?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.dataset?.filterheroid
        }
        if (filterType && heroId) {
            switch (filterType) {
                case "include": {
                    const index = heroFilterInclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterInclude = [...heroFilterInclude]
                        newHeroFilterInclude.splice(index, 1)
                        setHeroFilterInclude(newHeroFilterInclude)
                        navigateTo(undefined,undefined,newHeroFilterInclude,undefined)
                        updateHeroPackInfo(minWinRate, minFights, newHeroFilterInclude, heroFilterExclude)
                    }
                    break;
                }
                case "exclude": {
                    const index = heroFilterExclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterExclude = [...heroFilterExclude]
                        newHeroFilterExclude.splice(index, 1)
                        setHeroFilterExclude(newHeroFilterExclude)
                        navigateTo(undefined,undefined,undefined,newHeroFilterExclude)
                        updateHeroPackInfo(minWinRate, minFights, heroFilterInclude, newHeroFilterExclude)
                    }
                    break;
                }
                case "mwr": {
                    setMinWinRate(0)
                    navigateTo(undefined,0,undefined,undefined)
                    updateHeroPackInfo(0, minFights, heroFilterInclude, heroFilterExclude)
                    break;
                }
                case "mf": {
                    setMinFights(0)
                    navigateTo(0,undefined,undefined,undefined)
                    updateHeroPackInfo( minWinRate, 0, heroFilterInclude, heroFilterExclude)
                    break;
                }
            }
        }
    }
    const renderFilterBar = function () {
        const items = []
        for (const x of heroFilterInclude) {
            items.push(<Chip
                data-filtertype={"include"}
                data-filterheroid={x}
                label={"+" + HeroData.getHeroById(x)?.heroName || "" + x}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        for (const x of heroFilterExclude) {
            items.push(<Chip
                data-filtertype={"exclude"}
                data-filterheroid={x}
                label={"-" + HeroData.getHeroById(x)?.heroName || "" + x}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        if (minFights > 0) {
            items.push(<Chip
                data-filtertype={"mf"}
                data-filterheroid={1}
                label={<Box className={"filterBarLabel"}><img src={"./attack_w.png"}/><span>{minFights}</span></Box>}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        if (minWinRate > 0) {
            items.push(<Chip
                data-filtertype={"mwr"}
                data-filterheroid={1}
                label={<Box className={"filterBarLabel"}><img src={"./trophy_a_w.png"}/><span>{minWinRate + "%"}</span></Box>}
                onClick={onFilterClick}
                onDelete={handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }

        return (<Box className={"filterBar"}>
            {items}
        </Box>)
    }
    const onHeroSelect=function () {}
    const renderHeroPacks = function () {
        const items = []
        const emptyMap = new Map<number, number>()
        for (const i in heroPacks) {
            items.push(<SelectedHeroPack additionalClasses={["withCurrentStat"]} heroPack={heroPacks[i]}
                                         heroProbs={emptyMap}
                                         onHeroSelect={onHeroSelect} selectedHeroes={heroFilterInclude}
                                         key={heroPacks[i].hIds.join(",")}/>)
        }
        return items
    }
    return (<Box className={"counterPackScreen"}>
        <HeaderBlock openMenuFunction={props.openMenuFunction}
                     message={headerMessage}/>
        {requestsInProgress > 0 && (
            <Box>{t('Loading')}...<HourglassBottomIcon/></Box>
        )
        }
        {isUpdateOccurred && requestsInProgress === 0 && (
            <Box>
                <Box><Divider>{t('Filter')}<Button className={"svgButton"}><FilterAltIcon
                    onClick={onFilterClick}/></Button></Divider>
                    {filterOpened && (
                        <CounterPackFilterModal onClose={handleModalClose} memoArray={memoArray} gMode={props.gMode}/>
                    )}
                    {renderFilterBar()}
                </Box>
                {heroPacks.length === 0 && (<Box className={"counterPackNotFound"}>
                    <Box>{t('NotFoundPacksInStat')}</Box>
                    <span>{t('TryPackToAddToQueue')}</span>
                    <Button sx={{
                        color: 'white',
                        bgcolor: 'primary.light'
                    }}>{t('TryToFindPack')}</Button>
                </Box>)}
                {heroPacks.length > 0 && (<Box> <Divider>{t('Packs')}</Divider>
                        <Box className={"counterPackResultPacks"}>
                            {renderHeroPacks()}
                        </Box>
                    </Box>
                )}
            </Box>)}

    </Box>)

}