export class HURLS {
    static items = {
        faq: {id: 0, name: "FAQ", url: "faq"},
        draftGame: {id: 1, name: "DraftGame", url: "draftGame"},
        myPacks: {id: 2, name: "MyPacks", url: "myPacks"},
        fightResults: {id: 3, name: "FightsResults", url: "fightResults"},
        counterPack: {id: 4, name: "WhoWonByTeam", url: "counterPack"},
        attackPack: {id: 5, name: "WhoLostByTeam", url: "attackPack"},
        topAttack: {id: 6, name: "TopAttack", url: "topAttack"},
        topDefence: {id: 7, name: "TopDefence", url: "topDefence"},
        hydraFire: {id: 8, name: "TopHydraFire", url: "hydraFire"},
        hydraWater: {id: 9, name: "TopHydraWater", url: "hydraWater"},
        hydraEarth: {id: 10, name: "TopHydraEarth", url: "hydraEarth"},
        hydraWind: {id: 11, name: "TopHydraWind", url: "hydraWind"},
        hydraLight: {id: 12, name: "TopHydraLight", url: "hydraLight"},
        hydraDark: {id: 13, name: "TopHydraDark", url: "hydraDark"},
        queue: {id: 14, name: "Queue", url: "queue"},
    }
    static default=HURLS.items.faq
    static getIdByURL(url:string) {
        for(const x in HURLS.items) {
            // @ts-ignore
            if (HURLS.items[x].url.includes(url)) return HURLS.items[x].id
        }
        return 0
    }
    static getMenuItems() {
        const result:{id:number,name:string,url:string}[]=[]
        for(const x in HURLS.items) {
            // @ts-ignore
            result.push(HURLS.items[x])
        }
        return result
    }
    static itemsArray=HURLS.getMenuItems()
}