import React, {useEffect, useState} from "react";
import {Box} from "@mui/material";
import {HeroData} from "../types";
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import DoDisturbOnOutlinedIcon from '@mui/icons-material/DoDisturbOnOutlined';
import {MySettings} from "../types/MySettings";

interface IProps {
    heroId: number,
    heroProb?: number,
    onClick: any,
    additionalClasses: string[],
    isAdded?: boolean,
    isRemoved?: boolean,
    health?: number,
    energy?: number,
    damageDealt?: number,
    damageReceived?: number,
    healed?: number,
    order?: number
}

function getWindowDimensions() {
    const {innerWidth: width, innerHeight: height, devicePixelRatio: devicePixelRatio} = window;
    return {
        width,
        height,
        devicePixelRatio
    };
}

export default function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    return windowDimensions;
}


export function HeroModernIcon(props: IProps) {
    const onClick = function (e: any) {
        if (e?.target?.dataset?.heroid > 0) {
            props.onClick(~~e.target.dataset.heroid)
        }
    }

    let hero = HeroData.getHeroById(props.heroId)
    //hero = HeroData.getHeroById(19);
    const heroProb = props.heroProb || 0
    if (!hero) {
        hero = HeroData.emptyHero
    }
    let boxClassName = "heroModernIcon " + props.additionalClasses.join(" ")
    const {height, width} = useWindowDimensions();
    //let scale=0
    let size = (width + height) / 17
    if (props.additionalClasses.indexOf("heroIconSmall") >= 0) {
        if (MySettings.mainVersion === 1) {
            size = (0.8 * width + 0.8 * height) / 21
        } else {
            size = (0.8 * width + 0.8 * height) / 18
        }
    }
    if (props.additionalClasses.indexOf("selectedHeroPackIcons") >= 0) {
        size = width / 7
    }
    if (props.additionalClasses.indexOf("draftIcon")>=0) {
        size = Math.min(0.8 * width/8,0.8 * height/6)
    }
    if (size > 80) {
        size = 80
    }
    if (size < 10) {
        size = 10
    }
    size = size * window.visualViewport.scale


    const style = {width: size, height: size}
    return (
        <Box className={boxClassName} data-heroid={hero.heroId} onClick={onClick} sx={{order: props.order || 0,width:size+2,height:size+2}}>
            {props.isAdded !== undefined && props.isAdded && (
                <span className={"someHeroIcon"}><AddCircleOutlineRoundedIcon/></span>)}
            {props.isRemoved !== undefined && props.isRemoved && (
                <span className={"someHeroIcon"}><DoDisturbOnOutlinedIcon/></span>)}
            <span className={"heroModernIconName"}
                  style={{fontSize: size / 7.5 + 'px'}}>{hero.heroName + (hero.heroId > 0 ? "(" + hero.heroId + ")" : "")}</span>
            {props.heroProb!==undefined && (<span className={"heroModernIconProb"}
                  style={{fontSize: size / 7.5 + 'px'}}>{heroProb > 0 ? heroProb.toFixed(2) + "%" : ""}</span>)}
            <img alt={""}
                 src={"hero/" + HeroData.padLeadingZeros(hero.heroId, 4) + ".png"} style={style}/>
            {(HeroData.isNNAndNotNegative(props.damageDealt) || HeroData.isNNAndNotNegative(props.damageReceived) || HeroData.isNNAndNotNegative(props.healed)) && (
                <div className={"heroStats"}>
                    <span style={{fontSize: size / 7 + 'px'}}><img src={"./damagedealt.png"}
                                                                   alt={""}/>{HeroData.formatK(props.damageDealt)}</span>
                    <span style={{fontSize: size / 7 + 'px'}}><img src={"./damagerecieved.png"}
                                                                   alt={""}/>{HeroData.formatK(props.damageReceived)}</span>
                    <span style={{fontSize: size / 7 + 'px'}}><img src={"./heal.png"}
                                                                   alt={""}/>{HeroData.formatK(props.healed)}</span>
                </div>
            )}
            {(HeroData.isNNAndNotNegative(props.health) || HeroData.isNNAndNotNegative(props.energy)) && (
                <div className={"HEBars"}>
                    <div style={{
                        width: ~~HeroData.nvl(props.health, 0) + '%',
                    }} className={"healthBar"}/>
                    <div style={{
                        width: ~~HeroData.nvl(props.energy, 0) + '%',
                    }} className={"energyBar"}/>
                </div>)}
        </Box>
    )
}
