    static cloneHeroPack(heroPack:IHeroPack) {
        if (!heroPack) return HeroData.getEmptyHeroPack();
        const newHeroPack:IHeroPack= {
            hIds: [...heroPack.hIds]
        }
        if (heroPack.extendedInfo) {
            const hp=heroPack.extendedInfo;
            newHeroPack.extendedInfo ={
                statusInfo: hp.statusInfo,
                wra: hp.wra,
                wrd: hp.wrd,
                ca: hp.ca,
                cd: hp.cd,
                wa: hp.wa,
                wd: hp.wd,
                cc: hp.cc,
                cwr: hp.cwr,
                hf: hp.hf,
                hw: hp.hw,
                ha: hp.ha,
                hd: hp.hd,
                he: hp.he,
                hl: hp.hl,
                h1h: hp.h1h,
                h2h: hp.h2h,
                h3h: hp.h3h,
                h4h: hp.h4h,
                h5h: hp.h5h,
                h1dd: hp.h1dd,
                h2dd: hp.h2dd,
                h3dd: hp.h3dd,
                h4dd: hp.h4dd,
                h5dd: hp.h5dd,
                h1dr: hp.h1dr,
                h2dr: hp.h2dr,
                h3dr: hp.h3dr,
                h4dr: hp.h4dr,
                h5dr: hp.h5dr,
                h1dh: hp.h1dh,
                h2dh: hp.h2dh,
                h3dh: hp.h3dh,
                h4dh: hp.h4dh,
                h5dh: hp.h5dh,
            }
        }
        return newHeroPack;
    }
    static isInMyPacks(hp:IHeroPack) {
        const hIdsString=hp.hIds.sort(HeroData.sortByNumber).join(",")
        console.log("isInMyPacks:"+hIdsString+":  "+JSON.stringify(HeroData.getMyPacks()))
        for(const i in HeroData.get_instance()._myPacks) {
            if (HeroData.get_instance()._myPacks[i].hIds.sort(HeroData.sortByNumber).join(",")===hIdsString) {
                return true
            }
        }
        return false;
    }
    static saveFilter(memoArray:any) {
        localStorage.setItem("filter", JSON.stringify(memoArray));
    }

    static loadFilter() {
        return JSON.parse(localStorage.getItem("filter") || "{}")
        //localStorage.setItem("filter", JSON.stringify(memoArray));
    }



    static addToMyPacksStore(hIds:IHeroPack) {
        const hd=HeroData.get_instance()
        //hd.updateMyPacksFromStore();
        if (HeroData.isInMyPacks(hIds)) return -1;
     //   console.log("addToMyPacksStore1:"+JSON.stringify(HeroData._myPacks))
        hd._myPacks.push(HeroData.cloneHeroPack(hIds))
      //  console.log("addToMyPacksStore2:"+JSON.stringify(HeroData._myPacks))
        HeroData.saveMyPacks()
        //localStorage.setItem("myPacks", JSON.stringify(HeroData._myPacks));
       // console.log("addToMyPacksStore3:"+localStorage.getItem("myPacks"))
    }
    static saveMyPacks() {
        let savedPacks=[]
        const hd=HeroData.get_instance()
        for(const i in hd._myPacks) {
            savedPacks.push(hd._myPacks[i].hIds)
        }
        localStorage.setItem("myPacks", JSON.stringify(savedPacks));
        hd._myPacks=hd.updateMyPacksFromStore()
    }

    static removeFromMyPacksStore(heroPack:IHeroPack) {
        const hd=HeroData.get_instance()
        //HeroData.updateMyPacksFromStore();
        const hIds=heroPack.hIds.sort(HeroData.sortByNumber).join(",")
        for(let i=0;i<hd._myPacks.length;i++) {
            if (hd._myPacks[i].hIds.sort(HeroData.sortByNumber).join(",")===hIds) {
                hd._myPacks.splice(i,1)
                HeroData.saveMyPacks();
            }
        }
    }

    public updateMyPacksFromStore() {
        const saved = localStorage.getItem("myPacks") || "[]";
        const initialValue = JSON.parse(saved) || [];
        const newHeroPacks:IHeroPack[]=[]
        if (Array.isArray(initialValue) && initialValue.length>0) {
            for (const i in initialValue) {
                const x=initialValue[i]
                let newHIds:number[]=[]
                if (Array.isArray(x) && x.length>0 && x.length<=5) {
                    for (const j in x) {
                        if (this.allHeroesIds.includes(x[j])) {
                            newHIds.push(x[j])
                        }
                    }
                    if (newHIds.length>0) {
                        const newHeroPack=HeroData.getEmptyHeroPack()
                        newHeroPack.hIds=newHIds
                        newHeroPacks.push(newHeroPack)
                    }
                }
            }
        }

        //console.log("updateMyPacksFromStore Done:"+JSON.stringify(newHeroPacks))
        return newHeroPacks;
    }
    static nvl(value:any,newvalue:any) {
        if (HeroData.isNN(value)) {
            return value
        } else {
            return newvalue
        }
    }
    static isNN(value:any) {
        return value!==undefined && value !== null
    }

    static isNNAndNotNegative(value:any) {
        return value!==undefined && value !== null && value>=0
    }
    static formatK(value: number | undefined | null) {
        if (value!==undefined && value !== null) {
            let result
            if (value>1000000) {
                result=(value/1000000).toFixed(1)+"M"
            } else if (value>1000) {
                result=(value/1000).toFixed(1) + "k"
            } else {
                result=""+~~value
            }
            return result //.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+suffix
        } else {
            return "?"
        }
    }
    static constructHeroFilterNew(pHeroFilter: number[]) {
        let result = "%"
        pHeroFilter.sort((a,b) => {
            return a-b
        })
        for (let i = 0; i < pHeroFilter.length; i++) {
            result += ':' + pHeroFilter[i] + ':%'
        }
        console.log('HFE',result)
        return escape(result)
    }
    static translateHeroIdsArray(x:number[] | string[]) {
        let result=""
        if (x && Array.isArray(x)) {
            x.sort((a,b) => {
                return (HeroData.getHeroById(~~a)?.battleOrder || 1000)-(HeroData.getHeroById(~~b)?.battleOrder || 1000)
            })
            for(let i=0;i<5 && i<x.length;i++) {
                const hero=HeroData.getHeroById(~~x[i])
                if (hero) {
                    result+=hero.heroName
                }
                if (i!=4) result+=","
            }
        }

        return result
    }

    static isPackHasBoss(a:number[]) {
        let r=false
        if (a) {
            for( const x of a) {
                r = r || (x>=8010 && x<=8015)
            }
        }
        return r
    }
