import {useLocation, useNavigate} from "react-router-dom";
import {useTranslation} from "react-i18next";
import React, {useState} from "react";
import {Box} from "@mui/material";
import {HeaderBlock} from "./HeaderBlock";
import MenuIcon from "@mui/icons-material/Menu";

export interface IProps {
    openMenuFunction: any,
}

export function FAQScreen(props: IProps) {
    const navigate = useNavigate();
    const location = useLocation();
    const { t } = useTranslation();
    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }
    const [headerMessage, setHeaderMessage] = useState(t('FAQ').toString());
    return (        <Box className={"counterPackScreen"}>
        <HeaderBlock openMenuFunction={props.openMenuFunction}
                     message={headerMessage}/>
        <Box sx={{'padding': '1em 1em 1em 1em'}}>
            <h3>1. {t('WhatItIsTheService')}?</h3>
            <p>
                {t('WhatItIsTheServiceAnswer')}
            </p>
            <h3>2. {t('HowToUseIt')}?</h3>
            <p>
                {t('ClickOnTheMenuIcon')} <MenuIcon/> {t('AndWatchMenuItems')}
            </p>
            <h3>3. {t('HowToContactServiceAdmin')}?</h3>
            <p>
                {t('WriteCommentInTG')} <a href={"https://t.me/gelonsoft"}>@gelonsoft</a>
            </p>
        </Box>
    </Box>)
}