import {HeaderBlock} from "./HeaderBlock";
import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {Box, Button} from "@mui/material";
import {HeroData} from "../types";
import {HeroModernIcon} from "./HeroModernIcon";
import {SelectedHeroPack} from "./SelectedHeroPack";
import {IHeroPack} from "../types/IHeroPack";
import {HPusherClient} from "../utils/HPusherClient";
import SyncProblemIcon from '@mui/icons-material/SyncProblem';
import HourglassEmptyTwoToneIcon from '@mui/icons-material/HourglassEmptyTwoTone';
import DoneAllRoundedIcon from '@mui/icons-material/DoneAllRounded';
import RemoveDoneRoundedIcon from '@mui/icons-material/RemoveDoneRounded';


export interface IProps {
    openMenuFunction: any,
}

export function DraftScreen(props: IProps) {
    const {t} = useTranslation();
    const [headerMessage, setHeaderMessage] = useState(t('Choose1Of3Heroes').toString());
    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }
    const [isInitialized, setIsInitialized] = useState(false)
    const [stepNum, setStepNum] = useState(0)
    const [mode, setMode] = useState(0)
    const [requestsInProgress, setRequestsInProgress] = useState(0)
    const [stepHeroesA, setStepHeroesA] = useState(HeroData.shuffleArray([...HeroData.get_instance().heroIdsOrderedByName]).splice(0, 15) as number[])
    const [stepHeroesD, setStepHeroesD] = useState(HeroData.shuffleArray([...HeroData.get_instance().heroIdsOrderedByName]).splice(0, 15) as number[])
    const tempHeroPack = HeroData.getEmptyHeroPack()
    for (let j = 0; j < 5; j++) {
        tempHeroPack.hIds.push(stepHeroesA[j * 3 + HeroData.randomInt(2)])
    }
    const [round, setRound] = useState(1)
    const [roundWins, setRoundWins] = useState(0)

    const [heroPackA, setHeroPackA] = useState(tempHeroPack)
    const [heroPackD, setHeroPackD] = useState(HeroData.getEmptyHeroPack())
    const [heroPackTemp, setHeroPackTemp] = useState(HeroData.getEmptyHeroPack())
    const [fightResult, setFightResult] = useState(-1)
    const emptyFunction = () => {
    }

    useEffect(()=>{
        if (stepNum<5) {
            changeHeaderMessage(t('Choose1Of3Heroes'))
        } else {
            changeHeaderMessage(t('Results'))
        }
    },[stepNum])

    useEffect(() => {
        if (!isInitialized) {
            setIsInitialized(true)
        }
    });
    const renderEnemyPanel = function () {
        const items = []
        const showItems = [true, true, true, true, true, true, true, true, true, true, true, true, true, true, true]

        items.push(<Box key={100}>{t(mode === 1 ? 'DefenderPack' : 'AttackerPack')}</Box>)
        for (let j = 0; j < 5; j++) {
            const icons = []
            for (let i = j * 3; i <= j * 3 + 2; i++) {
                showItems[i] = showItems[i] && j <= stepNum
                if (mode === 1 || stepNum === 5) {
                    showItems[i] = showItems[i] && (heroPackA.hIds.includes(stepHeroesA[i]))
                }
                icons.push(<HeroModernIcon heroId={showItems[i] ? stepHeroesA[i] : -1} onClick={emptyFunction}
                                           additionalClasses={["draftIcon"]} key={i}/>)
            }
            items.push(<Box key={j}>{icons}</Box>)
        }
        return (<React.Fragment>{items}</React.Fragment>)
    }
    const getFightResult = function (pHeroPackA: IHeroPack, pHeroPackD: IHeroPack) {
        console.log('getFightResult entry')
        setRequestsInProgress(requestsInProgress + 1)
        HPusherClient.sendFight(pHeroPackA.hIds, pHeroPackD.hIds, 1, (data) => {

            if (data && data.r && Array.isArray(data.r) && data.r.length === 1 && data.r[0].r >= 0 && data.r[0].r <= 1) {
                console.log('getFightResult data ok', data)
                let result = data.r[0].r
                if (mode === 0) {
                    result = data.r[0].r === 0 ? 1 : 0
                }
                setFightResult(result)
                setRoundWins(roundWins + result)
            } else {
                console.log('getFightResult data bad', data)
                setFightResult(-1)
            }
            setRequestsInProgress(0)
        }, (data) => {
            setRequestsInProgress(0)
            setFightResult(-1)
            console.log('errorCallback DraftScreen')
        })
    }


    const onPickClick = function () {
        let newStepNum = stepNum + 1
        console.log('onPickClick', stepNum)
        let newHeroPack = heroPackD
        if (heroPackTemp.hIds.length > heroPackD.hIds.length) {
            newStepNum = stepNum + 1
            newHeroPack = heroPackTemp
            setStepNum(newStepNum)
            setHeroPackD(newHeroPack)
        }
        if (newStepNum === 5) {
            getFightResult(mode === 0 ? heroPackA : newHeroPack, mode === 0 ? newHeroPack : heroPackA)
        }
    }

    const retrySend = function () {
        getFightResult(mode === 0 ? heroPackA : heroPackD, mode === 0 ? heroPackD : heroPackA)
    }

    const nextRound = function () {
        if (fightResult >= 0) {
            setMode(round % 2)
            setRound(round + 1)
            setHeroPackD(HeroData.getEmptyHeroPack)
            setHeroPackTemp(HeroData.getEmptyHeroPack)
            setRequestsInProgress(0)

            setFightResult(-1)
            const newStepHeroesA = HeroData.shuffleArray([...HeroData.get_instance().heroIdsOrderedByName]).splice(0, 15) as number[]
            const newStepHeroesD = HeroData.shuffleArray([...HeroData.get_instance().heroIdsOrderedByName]).splice(0, 15) as number[]
            setStepHeroesA(newStepHeroesA)
            setStepHeroesD(newStepHeroesD)
            const tempHeroPack = HeroData.getEmptyHeroPack()
            for (let j = 0; j < 5; j++) {
                tempHeroPack.hIds.push(newStepHeroesA[j * 3 + HeroData.randomInt(2)])
            }
            setHeroPackA(tempHeroPack)
            setStepNum(0)
        }
    }


    const onHeroChooseSelect = function (heroId: number) {
        if (heroId > 0) {
            let index = heroPackTemp.hIds.indexOf(heroId)
            const newHeroPack = HeroData.getEmptyHeroPack()
            newHeroPack.hIds = heroPackTemp.hIds
            if (index >= 0) {
                newHeroPack.hIds.splice(index, 1)
                setHeroPackTemp(newHeroPack)
            } else {
                newHeroPack.hIds = [...heroPackD.hIds]
                newHeroPack.hIds.push(heroId)
                setHeroPackTemp(newHeroPack)
            }
        }
    }

    return (
        <Box className={"counterPackScreen"}>
            <HeaderBlock openMenuFunction={props.openMenuFunction}
                         message={headerMessage}/>
            <Box sx={{display: 'flex', alignItems: 'center', flexFlow: 'column',alignSelf: 'flex-start'}}>
                <Box>{t('Round')}: #{round}, {t('wins')}: {roundWins}</Box>
                <Box className={"draftPanels"}>
                    <Box className={"draftEnemyPanel"}>{renderEnemyPanel()}</Box>
                    <Box className={"draftPlayerPanel"} sx={{flexFlow: 'column', display: 'flex', alignItems: 'center', backgroundColor: '#321911'}}>
                        <Box>{t(mode === 0 ? 'DefenderPack' : 'AttackerPack')}</Box>
                        {stepNum < 5 && (
                            <Box>

                                <Box className={"draftChoose3"} key={1}>
                                    <HeroModernIcon heroId={stepHeroesD[3 * stepNum]} onClick={onHeroChooseSelect}
                                                    additionalClasses={["draftIcon"]} key={0}/>
                                    <HeroModernIcon heroId={stepHeroesD[3 * stepNum + 1]} onClick={onHeroChooseSelect}
                                                    additionalClasses={["draftIcon"]} key={1}/>
                                    <HeroModernIcon heroId={stepHeroesD[3 * stepNum + 2]} onClick={onHeroChooseSelect}
                                                    additionalClasses={["draftIcon"]} key={2}/>
                                </Box>
                                <Box key={2} sx={{display: 'flex', flexFlow: 'column',alignItems:'center'}}>
                                    <SelectedHeroPack heroPack={heroPackTemp} additionalClasses={[]}
                                                      additionalIconClasses={["draftIcon"]}
                                                      heroProbs={HeroData.emptyHeroProbMap} onHeroSelect={emptyFunction}
                                                      selectedHeroes={[stepHeroesD[3 * stepNum], stepHeroesD[3 * stepNum + 1], stepHeroesD[3 * stepNum + 2]]}/>

                                    {heroPackTemp.hIds.length === heroPackD.hIds.length && (
                                        <Button sx={{bgcolor: 'primary.light'}} className={"myButton"}
                                                variant={"outlined"} onClick={emptyFunction}>...</Button>
                                    )}
                                    {heroPackTemp.hIds.length > heroPackD.hIds.length && (
                                        <Button sx={{bgcolor: 'primary.light'}} className={"myButton"}
                                                onClick={onPickClick}>{t('Pick')}</Button>
                                    )}
                                </Box>
                            </Box>
                        )}
                        {stepNum === 5 && (
                            <Box sx={{display: 'flex', flexFlow: 'column',alignItems:'center'}}>
                                <SelectedHeroPack heroPack={heroPackD} additionalClasses={[]}
                                                  additionalIconClasses={["draftIcon"]}
                                                  heroProbs={HeroData.emptyHeroProbMap} onHeroSelect={emptyFunction}
                                                  selectedHeroes={[]}/>
                                {requestsInProgress > 0 && (<span><HourglassEmptyTwoToneIcon/>{t('Loading')}</span>)}
                                {requestsInProgress === 0 && fightResult >= 0 && (
                                    <React.Fragment>
                                        {fightResult===0 && (
                                            <Box sx={{fontSize: '2em'}}><RemoveDoneRoundedIcon/>{t( 'Lost')}<RemoveDoneRoundedIcon/></Box>
                                        )}
                                        {fightResult===1 && (
                                            <Box sx={{fontSize: '2em'}}><DoneAllRoundedIcon/>{t('Won')}<DoneAllRoundedIcon/></Box>
                                        )}
                                        <Box><Button sx={{bgcolor: 'primary.light'}} className={"myButton"}
                                                     onClick={nextRound}>{t('NextRound')}</Button></Box>
                                    </React.Fragment>
                                )}
                                {requestsInProgress === 0 && fightResult < 0 && (
                                    <React.Fragment>
                                        <Box>{t('Error')}</Box>
                                        <Button sx={{bgcolor: 'primary.light'}} className={"myButton"}
                                                onClick={retrySend}><SyncProblemIcon/>{t('Retry')}</Button>
                                    </React.Fragment>
                                )}
                            </Box>
                        )}
                    </Box>
                </Box>
            </Box>
        </Box>)
}