import React, {useState} from "react";
import {Box, Slide, Tab, Tabs} from "@mui/material";
import {CounterPackScreen} from "./CounterPackScreen";

import {
    TransitionGroup,
    CSSTransition
} from "react-transition-group";


import {HashRouter, Navigate, Route, Routes} from "react-router-dom"

/*import GLogin from "./GLogin";
import GLogout from "./GLogout";*/
import {useTranslation} from "react-i18next";
import {TopPacksScreen} from "./TopPacksScreen";
import {MyPacks} from "./MyPacks";
import {QueueScreen} from "./QueueScreen";
import {FightsResult} from "./FightsResult";
import {FAQScreen} from "./FAQScreen";
import {DraftScreen} from "./DraftScreen";
import {HURLS} from "../types/HURLS";
import {HeroData} from "../types";



export function MainScreen (props: any) {
    //if (!HeroData._myPacks) console.log("MainScreen:not initialized HeroData._myPacks")
    const location = window.location;
    const mainURL = window.location.pathname.replace(/[#?].*/g,'')

    const [navMenuIndex,setNavMenuIndex] = useState(HURLS.getIdByURL(location.pathname))
    const [navMenuOpened,setNavMenuOpened] = useState(false)
    const { t,  } = useTranslation();

    const changeMenu = function (event: any, newValue: string) {
        setNavMenuIndex(~~newValue)
    }

    const toggleMenu = function () {
        setNavMenuOpened(!navMenuOpened)
    }

    const menuTabs=[]
    for(const x of HURLS.getMenuItems()) {
        menuTabs.push(<Tab label={t(x.name)} id={"vertical-tab-"+x.id} aria-controls={"vertical-tabpanel-"+x.id} href={mainURL+"#/"+x.url} key={x.id}/>)
    }

    return (
        <HashRouter>
            <Box className={"mainScreen"}
                 sx={{flexGrow: 1, display: 'flex', width: '100vw', height: '100%', overflowX: 'hidden'}}>

                <Slide direction="right" in={navMenuOpened}>
                    <Box className={"menuBox"}>
                        <Tabs orientation="vertical"
                              variant="scrollable"
                              value={navMenuIndex}
                              onChange={changeMenu}
                              aria-label="Vertical tabs example"
                              sx={{borderRight: 1, borderColor: 'divider'}}>
                            {menuTabs}
                        </Tabs>
                        <Box className={"menuFadeBox"} onClick={toggleMenu}/>
                    </Box>
                </Slide>
                <Box role="tabpanel">
                    <TransitionGroup>
                        {/*
            This is no different than other usage of
            <CSSTransition>, just make sure to pass
            `location` to `Switch` so it can match
            the old location as it animates out.
          */}
                        <CSSTransition
                            key={location.pathname}
                            classNames="fade"
                            timeout={300}
                        >
                    <Routes>
                        <Route path={"/"+HURLS.items.myPacks.url} element={
                            <MyPacks openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.myPacks.url} key={HURLS.items.myPacks.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.fightResults.url} element={
                            <FightsResult openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.fightResults.url} key={HURLS.items.fightResults.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.counterPack.url} element={
                            <CounterPackScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.counterPack.url} findPackOption={"counter"} key={100+HURLS.items.counterPack.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.attackPack.url} element={
                            <CounterPackScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.attackPack.url} findPackOption={"attack"} key={200+HURLS.items.attackPack.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.topAttack.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.topAttack.url} gMode={"a"} key={HURLS.items.topAttack.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.topDefence.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.topDefence.url} gMode={"d"} key={HURLS.items.topDefence.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraFire.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraFire.url} gMode={"g8010"} key={HURLS.items.hydraFire.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraWater.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraWater.url} gMode={"g8011"} key={HURLS.items.hydraWater.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraEarth.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraEarth.url} gMode={"g8012"} key={HURLS.items.hydraEarth.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraWind.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraWind.url} gMode={"g8013"} key={HURLS.items.hydraWind.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraLight.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraLight.url} gMode={"g8014"} key={HURLS.items.hydraLight.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.hydraDark.url} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/"+HURLS.items.hydraDark.url} gMode={"g8015"} key={HURLS.items.hydraDark.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.queue.url} element={
                            <QueueScreen openMenuFunction={toggleMenu} key={HURLS.items.queue.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.faq.url} element={
                            <FAQScreen openMenuFunction={toggleMenu} key={HURLS.items.faq.id}
                            />
                        }/>
                        <Route path={"/"+HURLS.items.draftGame.url} element={
                            <DraftScreen openMenuFunction={toggleMenu} key={HURLS.items.draftGame.id}
                            />
                        }/>
                        <Route path={"*"} element={
                            <Navigate replace to={"/"+HURLS.default.url} />}
                        />
                    </Routes>
                        </CSSTransition>
                    </TransitionGroup>
                </Box>
            </Box>
        </HashRouter>
    )

}