export class HEventSystem {
    static timeOutPeriod=300000
    static idSeq=0
    static subscriptions={}
    static subscribe(eventType:string, callback:(data:any)=>void) {
        console.log("HEventSystem subscribe",eventType)
        const id = this.idSeq++
        // @ts-ignore
        if(!HEventSystem.subscriptions[eventType]) HEventSystem.subscriptions[eventType] = { }
        // @ts-ignore
        HEventSystem.subscriptions[eventType][id] = callback
        const unsubscribe = () => {
            console.log('HEventBus.unsubscribe',eventType,id)
            // @ts-ignore
            delete HEventSystem.subscriptions[eventType][id]
            // @ts-ignore
            if(Object.keys(HEventSystem.subscriptions[eventType]).length === 0) delete HEventSystem.subscriptions[eventType]
        }
        setTimeout(unsubscribe,HEventSystem.timeOutPeriod)
        return {
            unsubscribe: unsubscribe
        }
    }
    static publish(eventType:string, arg:any) {
        console.log('EventSystem Publish',eventType,arg);
        // @ts-ignore
        if(!HEventSystem.subscriptions[eventType])
            return
        // @ts-ignore
        Object.keys(HEventSystem.subscriptions[eventType]).forEach(key => HEventSystem.subscriptions[eventType][key](arg))
    }
}