import React from "react";
import {HeroPackSelected} from "./index";
import {Box, Chip, Divider} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";
import {MySettings} from "../types/MySettings";

interface IQueueItem {
    position: number,
    heroIdsA: IHeroPack,
    testQId?: number
}

interface IProps {
    queueItemsUser: IQueueItem[]
    queueItemsTopD: IQueueItem[]
    queueItemsTopA: IQueueItem[]
    queueItemsClassic: IQueueItem[]
}

interface IState {
    queueItemsUser: IQueueItem[],
    queueItemsTopD: IQueueItem[],
    queueItemsTopA: IQueueItem[],
    queueItemsClassic: IQueueItem[],
    requestsInProgress: number
}

export class HeroRouletteDefendersMode extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            queueItemsUser: props.queueItemsUser,
            queueItemsTopD: props.queueItemsTopD,
            queueItemsTopA: props.queueItemsTopA,
            queueItemsClassic: props.queueItemsClassic,
            requestsInProgress: 0
        }
        this.refreshQueue=this.refreshQueue.bind(this)
        this.refreshQueue()
        this.removeIcon=this.removeIcon.bind(this)
        MyNavigator.heroRouletteDefendersMode=this
    }

    removeIcon(e:any) {
        console.log('e',e)
        if (e && e.target && e.target.dataset && e.target.dataset.testqid) {
        fetch(MySettings.apiUrl+"queueRD/"+e.target.dataset.testqid, {
            method: "DELETE"
            }).finally(()=>{
                this.refreshQueue()
            })
        }
    }
    static num_word(value:number, words:string[]){
        value = Math.abs(value) % 100;
        var num = value % 10;
        if(value > 10 && value < 20) return words[2];
        if(num > 1 && num < 5) return words[1];
        if(num === 1) return words[0];
        return words[2];
    }

    static formatParameter(x:any,name:string,goodName:string,multiplier:number,unit:string,prec:number) {
        let res=""
        if (x[name] && x[name]!==null && x[name]!=="null") {
            res+=goodName+"="+(x[name]*multiplier).toFixed(prec).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+unit
            if (x[name+"min"] && x[name+"max"] && x[name+"cnt"]) {
                if (~~x[name+"min"]!==~~x[name+"max"]) {
                    res+=" ("+(x[name+"min"]*multiplier).toFixed(prec).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+unit
                    res+=" - "+(x[name+"max"]*multiplier).toFixed(prec).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+unit
                    res+="  "+(x[name+"cnt"]).toFixed(0).toString()
                    res+=" "+HeroRouletteDefendersMode.num_word(x[name+"cnt"],['','',''])+")"
                } else {
                    res+="("+(x[name+"cnt"]).toFixed(0).toString()
                    res+=" "+HeroRouletteDefendersMode.num_word(x[name+"cnt"],['','',''])+")"
                }

            }
            res+="\r\n"
        }
        return res

    }

    refreshQueue() {
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch(MySettings.apiUrl+"queueRD/", {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                if (result.items) {
                    const newQPlayer:IQueueItem[]=[]
                    const newQAttacker:IQueueItem[]=[]
                    const newQDefender:IQueueItem[]=[]
                    const newQClassic:IQueueItem[]=[]
                    const position:any={'u':1,'a':1,'d':1,'c':1}
                    for (const i in result.items) {
                        const x=result.items[i]
                        const heroIdsA=[]; for (let j=1;j<6;j++) {if (HeroData.getHeroById(~~x['d'+j])) {heroIdsA.push(~~x['d'+j])}}
                        let extText=""
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"sumwinrate","____",100,"%",1)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"avgwinrate","____",100,"%",1)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"cntwinpacks","_____",1,"",0)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"winsa","____",1,"",0)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"cntfightsa","____",1,"",0)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"sumlossrate","____",100,"%",1)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"avglossrate","____",100,"%",1)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"cntlosspacks","____",1,"",0)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"winsd","____",1,"",0)
                        extText+=HeroRouletteDefendersMode.formatParameter(x,"cntfightsd","____",1,"",0)

                        if (x.typ==='u') {
                            newQPlayer.push({position:position[x.typ]++,heroIdsA:{hIds:heroIdsA,extendedInfo:{statusInfo:extText}},testQId:x['testq_id']})
                        } else if (x.typ==='a') {
                            newQAttacker.push({position:position[x.typ]++,heroIdsA:{hIds:heroIdsA,extendedInfo:{statusInfo:extText}}})
                        } else if (x.typ==='d') {
                            newQDefender.push({position:position[x.typ]++,heroIdsA:{hIds:heroIdsA,extendedInfo:{statusInfo:extText}}})
                        } else if (x.typ==='c') {
                            newQClassic.push({position:position[x.typ]++,heroIdsA:{hIds:heroIdsA,extendedInfo:{statusInfo:extText}}})
                        }
                    }
                    this.setState({
                        queueItemsUser: newQPlayer,
                        queueItemsTopD: newQDefender,
                        queueItemsTopA: newQAttacker,
                        queueItemsClassic: newQClassic
                    })
                }
                }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    renderQueue(queueItems:IQueueItem[],showCloseButton:boolean) {
        const items=[]
        if (queueItems.length===0) {
            return (<Box></Box>)
        } else {
        queueItems.sort((a:any,b:any)=> {
            return a.position-b.position
        })
        for (const i in queueItems) {
            const x=queueItems[i]
            items.push(<Box className={"heroBoxQueueWithNum"} sx={{
                display: 'flex',
                bgcolor: 'primary.main',
                color: 'primary.contrastText',
                p: '0.5em 0.5em 0.5em 0.5em',
                marginBottom: '0.1em',
                borderRadius: '10px',
            }} key={i}>
                <Box sx={{
                    fontSize: '1em',
                    fontWeight: 700,
                    marginLeft: '0.1em',
                    marginRight: '0.5em'
                }}>{x.position}</Box>
                <Box><HeroPackSelected heroPack={x.heroIdsA} deselectHero={()=>{}}/>
            </Box>
                {showCloseButton && !MyNavigator.isCloseDisabled()?<Box><img data-testqid={x.testQId} className={"closeIcon"} alt="close" onClick={this.removeIcon} src={"close_icon.png"}/></Box>:null}
            </Box>)
        }
        return items
        }
    }



    render() {
        return (
            <Box>
            <Divider><Chip label={this.state.requestsInProgress>0?" ... !":"   .    "} onClick={this.refreshQueue}/></Divider>
            <Box>{this.renderQueue(this.state.queueItemsUser,true)}</Box>
                <Divider><Chip label={this.state.requestsInProgress>0?" ... !":"   .    "} onClick={this.refreshQueue}/></Divider>
                <Box>{this.renderQueue(this.state.queueItemsTopA,false)}</Box>
                <Divider><Chip label={this.state.requestsInProgress>0?" ... !":"   .    "} onClick={this.refreshQueue}/></Divider>
                <Box>{this.renderQueue(this.state.queueItemsTopD,false)}</Box>
                <Divider><Chip label={this.state.requestsInProgress>0?" ... !":"  .    "} onClick={this.refreshQueue}/></Divider>
                <Box>{this.renderQueue(this.state.queueItemsClassic,false)}</Box>
            </Box>
        )
    }
}

export default HeroRouletteDefendersMode