import {Channel as PusherClientChannel , default as PusherClient} from "pusher-js" ;
import {HEventSystem} from "./HEventSystem";
import {HeroData} from "../types";

export class HPusherClient {
    static initialized=HPusherClient.init()
    static pusherClient:PusherClient
    static serverChannel:PusherClientChannel
    static userId:number=-1
    static requestId=HeroData.randomInt(1000000)
    static init() {
        if (HPusherClient.initialized && HPusherClient.serverChannel?.subscribed) return;


        HPusherClient.pusherClient = new PusherClient('d46af5def4f753e7d4c2', {
            auth:{
                params: {
                    user_id:HPusherClient.userId
                }
            },
            cluster: 'eu',
            authEndpoint: "https://gelonsoft.green-heroes.com/pusher/auth"
            //authEndpoint: "http://localhost:4000/pusher/auth"
        })
        const sc=HPusherClient.pusherClient.subscribe('presence-fights');
        HPusherClient.serverChannel = sc
        console.log("SC",HPusherClient.serverChannel,sc)
        HPusherClient.serverChannel.bind("pusher:subscription_succeeded", function () {
            HPusherClient.serverChannel=sc
            console.log('GOOD',(HPusherClient.serverChannel as any)?.members)
            HPusherClient.initialized=true
            HPusherClient.serverChannel.bind('fight-complete', HPusherClient.fightCompleteCallback)
            HPusherClient.serverChannel.bind('fight-request', HPusherClient.requestCompleteCallback)
            let userId=localStorage.getItem('userId')
            if (userId && ~~userId>0) {
                HPusherClient.userId=~~userId
            } else {
                HPusherClient.userId=100+HeroData.randomInt(1000000)
                localStorage.setItem('userId',HPusherClient.userId.toString())
            }
        });
        return true
    }
    static fightCompleteCallback(data:any) {
        console.log('fightCompleteCallback',data)
        HEventSystem.publish('Service:OneFight:'+data.rid+":"+data.uid,data)
    }
    static requestCompleteCallback(data:any) {
        console.log('requestCompleteCallback',data)
        HEventSystem.publish('Service:OneFightRC:'+data.rid+":"+data.uid,data)
    }
    static sendFight(attacker:number[],defender:number[],num:number,callback:(data:any)=>void,errorCallback:(data:any)=>void) {
        try {
            HPusherClient.init()
            const rid = HPusherClient.requestId++
            HPusherClient.serverChannel.trigger('client-fight-request',{
                rt: 1,
                    uid: HPusherClient.userId,
                    rid: rid,
                    n: num,
                    a: attacker,
                    d: defender
            })
            console.log('sendFight', attacker, defender, num, rid, HPusherClient.userId)
            HEventSystem.subscribe('Service:OneFight:' + rid + ":" + HPusherClient.userId, callback)
            HEventSystem.subscribe('Service:OneFightRC:' + rid + ":" + HPusherClient.userId, (data) => {
                if (data.status < 0) {
                    errorCallback(undefined)
                }
            })
        } catch (e) {
            console.error('sendFight error2', attacker, defender, num,  HPusherClient.userId,  e)
            errorCallback(undefined)
        }
    }

}