import React, {useEffect, useState} from "react";
import {Box, Divider} from "@mui/material";
import {SelectedHeroPack} from "./SelectedHeroPack";
import {HeroData} from "../types";
import {HeroModernGrid} from "./HeroModernGrid";
import {IHeroPack} from "../types/IHeroPack";
import {HeaderBlock} from "./HeaderBlock";
import {useLocation,  useNavigate} from "react-router-dom";
import {CounterPackResults} from "./CounterPackResults";
import {MySettings} from "../types/MySettings";
import { useTranslation } from "react-i18next";

const myFindPackOptions = {
    counter: {
        findPackStatAPIUrl: 'findLostPackHeroesStat/'
    },
    attack: {
        findPackStatAPIUrl: 'findWinPackHeroesStat/'
    }
}

export interface IProps {
    openMenuFunction: any,
    findPackOption: string,
    mainURL:string,
}

export function CounterPackScreen(props: IProps) {
    const myOptions = props.findPackOption==='counter'?myFindPackOptions.counter:myFindPackOptions.attack
    const mainURL=props.mainURL
   /* const params = useParams();
    const match = useMatch("");*/
    const navigate = useNavigate();
    const location = useLocation();
    const { t } = useTranslation();
    const [headerMessage, setHeaderMessage] = useState(t('Choose5Heroes').toString());
    const templateHeroPack = HeroData.getEmptyHeroPack()
    const queryParams: any = new URLSearchParams(location?.search || "")
    const [isInitialized, setIsInitialized] = useState(false)
    if (queryParams.get("hids")) {
        const x = queryParams.get("hids")?.split(",")
        if (x && x.length <= 5) {
            for (const z of x) {
                templateHeroPack.hIds.push(~~z)
            }
        }
    }

    const [heroPack, setHeroPack] = useState(templateHeroPack);
    const [heroAvailable, setHeroAvailable] = useState([] as number[])
    const [heroProbs, ] = useState(new Map<number, number>())
    const [requestsInProgress, setRequestsInProgress] = useState(0)

    const updateAvailableHeroes = function (pHeroPack: IHeroPack) {
        const heroParams = [-2, -2, -2, -2, -2]
        for (let i = 0; i < 5 && i < pHeroPack.hIds.length; i++) {
            heroParams[i] = pHeroPack.hIds[i];
        }
        setRequestsInProgress(requestsInProgress + 2)
        let url = MySettings.apiUrl+myOptions.findPackStatAPIUrl
        fetch(url + heroParams.join("/"), {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                    let heroArray: number[] = []
                    if (result['items']) {
                        for (const j in result['items']) {
                            const x = result['items'][j]['d']
                            if (x) {
                                if (!heroArray.includes(~~x)) {
                                    heroArray.push(~~x)
                                }
                            }
                        }
                    }
                    setHeroAvailable(heroArray)
                },
                (error) => {
                    console.error("error req", error)
                    setHeroAvailable([])
                }
            ).finally(() => {
            setRequestsInProgress(requestsInProgress - 1)
        })

        url = MySettings.apiUrl+"packStat/"
        fetch(url + heroParams.join("/"), {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                    //let heroArray: number[] = []
                    if (result['items']) {
                        for (const j in result['items']) {
                            const x = result['items'][j]
                            if (x) {
                                const newHeroPack = HeroData.getEmptyHeroPack()
                                newHeroPack.hIds = pHeroPack.hIds
                                newHeroPack.extendedInfo = {
                                    statusInfo: "",
                                    ca: x.ca,
                                    cd: x.cd,
                                    wra: x.wra,
                                    wrd: x.wrd,
                                    wa: x.wa,
                                    wd: x.wd,
                                }
                                setHeroPack(newHeroPack)
                            }
                        }
                    }
                    //console.log('setHeroAvailable',heroArray)
                    //setHeroAvailable(heroArray)
                },
                (error) => {
                    console.error("error req", error)
                    //setHeroAvailable([])
                }
            ).finally(() => {
            setRequestsInProgress(requestsInProgress - 1)
        })


    }
    const updateHeroPackInfo = function (pHeroPack: IHeroPack) {
        setHeroPack(pHeroPack)
        updateAvailableHeroes(pHeroPack)
        if (pHeroPack.hIds.length !== 5) {
            setHeaderMessage(t('Choose5Heroes'))
        }
    }
    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }
    const onHeroSelect = function (heroId: number) {
        if (HeroData.get_instance().allHeroesIds.includes(heroId)) {
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    heroPack.extendedInfo = {statusInfo: ""}
                    navigate(mainURL+"?hids=" + heroPack.hIds.join(","))
                    updateHeroPackInfo(heroPack)
                }
            } else {
                heroPack.extendedInfo = {statusInfo: ""}
                heroPack.hIds.splice(index, 1);
                navigate(mainURL+"?hids=" + heroPack.hIds.join(","))
                updateHeroPackInfo(heroPack)
            }
        }
    }
    useEffect(() => {
        if (!isInitialized && heroPack.hIds.length === 5) {
            setIsInitialized(true)
            updateHeroPackInfo(heroPack)
        }
    });


    return (
        <Box className={"counterPackScreen"}>
            <HeaderBlock openMenuFunction={props.openMenuFunction}
                         message={headerMessage}/>
            {heroPack.hIds.length === 5 && (<Divider>{t('ChosenTeam')}</Divider>)}
            <SelectedHeroPack heroPack={heroPack} heroProbs={HeroData.get_instance().zeroHeroProbMap}
                              onHeroSelect={onHeroSelect} selectedHeroes={[]} additionalClasses={[]}/>
            {heroPack.hIds.length < 5 && (
                <HeroModernGrid additionalClasses={[]} heroProb={heroProbs} onClick={onHeroSelect}
                                selectedHeroes={heroPack.hIds} highlightedHeroes={heroAvailable}/>
            )}
            {heroPack.hIds.length === 5 && (
                <CounterPackResults changeHeaderMessageFunction={changeHeaderMessage} heroPack={heroPack}
                                    params={queryParams} findPackOption={props.findPackOption}
                                    mainURL={mainURL + (heroPack.hIds.length > 0 ? "?hids=" + heroPack.hIds.join(",") : "")}/>
            )}
        </Box>
    )

}

