import {HeaderBlock} from "./HeaderBlock";
import React, {useEffect, useState} from "react";
import {Box, Button, Divider} from "@mui/material";
import {useTranslation} from "react-i18next";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernGrid} from "./HeroModernGrid";
import {SelectedHeroPack} from "./SelectedHeroPack";


export interface IProps {
    openMenuFunction: any,
    mainURL:string,
}

export function MyPacks(props: IProps) {
  /*  const mainURL=props.mainURL
    const navigate = useNavigate();
    const location = useLocation();*/
    const { t } = useTranslation();
    const [headerMessage, setHeaderMessage] = useState(t('MyPacks').toString());
    const [myPacks, setMyPacks] = useState(() => {
        // getting stored value
        console.log("MyPacks:"+JSON.stringify(HeroData.getMyPacks()));
        return HeroData.getMyPacks()
    });
    const [openedMyPacksMenu,setOpenedMyPacksMenu] = useState(myPacks.length===0)
    const [heroPack,setHeroPack] = useState(HeroData.getEmptyHeroPack)

    useEffect(() => {
        HeroData.get_instance()._myPacks=[...myPacks];
        HeroData.saveMyPacks();
        //HeroData.updateMyPacksFromStore()
     /*   let savedPacks=[]
        for(const i in myPacks) {
            savedPacks.push(myPacks[i].hIds)
        }
   //     console.log("MyPacks:useEffect1:"+JSON.stringify(myPacks));
   //     console.log("MyPacks:useEffect2:"+JSON.stringify(HeroData.getMyPacks()));
       // localStorage.setItem("myPacks", JSON.stringify(savedPacks));
        HeroData.updateMyPacksFromStore()
        console.log("MyPacks:useEffect3:"+JSON.stringify(myPacks));
        console.log("MyPacks:useEffect4:"+JSON.stringify(HeroData.getMyPacks()));*/
    }, [myPacks]);

    const toggleMyPacksMenu=function () {
        setOpenedMyPacksMenu(!openedMyPacksMenu)
    }

    const removeHeroPack=function(heroPack:IHeroPack) {
        const hIds=heroPack.hIds.join(",")
        for(let i=0;i<myPacks.length;i++) {
            if (myPacks[i].hIds.join(",")===hIds) {
                const newMyPacks=[...myPacks]
                newMyPacks.splice(i,1)
                setMyPacks(newMyPacks)
            }
        }

    }

    const onHeroSelect = function (heroId: number) {
        if (HeroData.get_instance().allHeroesIds.includes(heroId)) {
            const index = heroPack.hIds.indexOf(heroId)
            if (index < 0) {
                if (heroPack.hIds.length < 5) {
                    heroPack.hIds.push(heroId)
                    setHeroPack(HeroData.cloneHeroPack(heroPack))
                }
            } else {
                heroPack.hIds.splice(index, 1);
                setHeroPack(HeroData.cloneHeroPack(heroPack))
            }
        }
    }

    const finishEdit = function() {
        setOpenedMyPacksMenu(false)
        const hIds=heroPack.hIds.join(",")
        for(const i in myPacks) {
            if (myPacks[i].hIds.join(",")===hIds) {
                return
            }
        }
        const newMyPacks=[...myPacks]
        newMyPacks.push(HeroData.cloneHeroPack(heroPack))
        //setHeroPack({hIds:heroPack.hIds,extendedInfo:{statusInfo: ""}})
        setMyPacks(newMyPacks)
    }
    const renderHeroPacks = function () {
        const items = []
        const emptyMap = new Map<number, number>()
        for (const i in myPacks) {
            items.push(<SelectedHeroPack additionalClasses={[]} heroPack={myPacks[i]}
                                         heroProbs={emptyMap}
                                         onHeroSelect={onHeroSelect} selectedHeroes={[]} onRemoveFunction={removeHeroPack}
                                         key={myPacks[i].hIds.join(",")}/>)
        }
        return items
    }

    return (
    <Box className={"counterPackScreen"}>
        <HeaderBlock openMenuFunction={props.openMenuFunction}
                     message={headerMessage}/>
        {(!openedMyPacksMenu) && (<Button sx={{bgcolor: 'primary.light'}} className={"myButton"} onClick={toggleMyPacksMenu}>{t("Add")}</Button>)}
        {(openedMyPacksMenu) && (
            <React.Fragment>
            <Divider>{t('ChosenTeam')}</Divider>
            <SelectedHeroPack heroPack={heroPack} heroProbs={HeroData.get_instance().zeroHeroProbMap}
                          onHeroSelect={onHeroSelect} selectedHeroes={[]} additionalClasses={[]}/>
                {(heroPack.hIds.length<5) && (<Button sx={{bgcolor: 'primary.light'}} className={"myButton"} variant={"outlined"}>{t("...")}</Button>)}
                {(heroPack.hIds.length===5) && (<Button sx={{bgcolor: 'primary.light'}} className={"myButton"} onClick={finishEdit}>{t("Done")}</Button>)}
            <HeroModernGrid additionalClasses={[]}  onClick={onHeroSelect}
                            selectedHeroes={heroPack.hIds} highlightedHeroes={[]}/>
            </React.Fragment>
        )}
        {(myPacks.length>0) && (
            <Box className={"counterPackResultPacks"}>
                {renderHeroPacks()}
            </Box>
        )}
    </Box>
    )
}