import {Box, Button, Modal, Tooltip} from "@mui/material";
import {ModernSlider} from "./ModernSlider";
import {HeroModernGrid} from "./HeroModernGrid";
import React, {useState} from "react";
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined';
import UploadFileIcon from '@mui/icons-material/UploadFile';
import SimCardDownloadIcon from '@mui/icons-material/SimCardDownload';
import {HeroData} from "../types";

export function CounterPackFilterModal(props: any) {
    const [modalOpen, setModalOpen] = useState(true)
    const [heroFilterInclude, setHeroFilterInclude] = useState(props.memoArray.heroFilterInclude)
    const [heroFilterExclude, setHeroFilterExclude] = useState(props.memoArray.heroFilterExclude)
    const handleModalClose = function () {
        setModalOpen(false)
        props.memoArray.heroFilterInclude = heroFilterInclude
        props.memoArray.heroFilterExclude = heroFilterExclude
        props.onClose(props.memoArray)
    }
    const changeMinWinRate = function (value: number) {
        props.memoArray.minWinRate = value
    }
    const changeMinFights = function (value: number) {
        props.memoArray.minFights = value
    }
    const onHeroGridClick = function (heroId: number) {
        console.log('onHeroGridClick', heroId, heroFilterInclude, heroFilterExclude)
        const newHeroFilterInclude = [...heroFilterInclude]
        const newHeroFilterExclude = [...heroFilterExclude]
        const includeIndex = newHeroFilterInclude.indexOf(heroId)
        const excludeIndex = newHeroFilterExclude.indexOf(heroId)
        if (includeIndex >= 0) {
            newHeroFilterInclude.splice(includeIndex, 1)
            setHeroFilterInclude(newHeroFilterInclude)
            props.memoArray.heroFilterInclude=newHeroFilterInclude
            if (excludeIndex < 0) {
                newHeroFilterExclude.push(heroId)
                setHeroFilterExclude(newHeroFilterExclude)
                props.memoArray.heroFilterExclude=newHeroFilterExclude
            }
        } else if (excludeIndex >= 0) {
            newHeroFilterExclude.splice(excludeIndex, 1)
            setHeroFilterExclude(newHeroFilterExclude)
            props.memoArray.heroFilterExclude=newHeroFilterExclude
        } else {
            if (newHeroFilterInclude.length < 5) {
                newHeroFilterInclude.push(heroId)
                setHeroFilterInclude(newHeroFilterInclude)
                props.memoArray.heroFilterInclude=newHeroFilterInclude
            }
        }

    }
    const loadFilter=function ()  {
        const tempMemoArray=HeroData.loadFilter()
        if (tempMemoArray.minWinRate!==null) props.memoArray.minWinRate=~~tempMemoArray.minWinRate;
        if (tempMemoArray.minFights!==null) props.memoArray.minFights=~~tempMemoArray.minFights;
        if (tempMemoArray.heroFilterInclude!==null) {setHeroFilterInclude([...tempMemoArray.heroFilterInclude]); props.memoArray.heroFilterInclude=[...tempMemoArray.heroFilterInclude];}
        if (tempMemoArray.heroFilterExclude!==null) {setHeroFilterExclude([...tempMemoArray.heroFilterExclude]); props.memoArray.heroFilterExclude=[...tempMemoArray.heroFilterExclude];}
        console.log(JSON.stringify(props.memoArray))
    }
    const saveFilter=function ()  {
       HeroData.saveFilter(props.memoArray);
       console.log(JSON.stringify(props.memoArray))
    }


    return (
        <Modal
            className={"myModal"}
            open={modalOpen}
            onClose={handleModalClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box className={"counterPackResultModalBox"}>
                <Button className={"closeModal"} onClick={handleModalClose}><CancelOutlinedIcon/></Button>

                {props.memoArray.filterMaxWinRate>0 && (
                <Box className={"sliderRow"}>
                    {(props.gMode?.indexOf("g80")>=0) && (
                    <Tooltip title={"  "}><img src={"./damage.png"}/></Tooltip>
                    )}
                    {(props.gMode === 'd') && (
                        <Tooltip title={"  "}><img src={"./trophy_d_w.png"}/></Tooltip>
                    )}
                    {(props.gMode === 'a' || props.gMode === undefined || props.gMode === null) && (
                        <Tooltip title={"  "}><img src={"./trophy_a_w.png"}/></Tooltip>
                    )}
                    <ModernSlider min={props.memoArray.filterMinWinRate} max={props.memoArray.filterMaxWinRate}
                                  label={"some2"}
                                  initial={props.memoArray.minWinRate}
                                  callback={changeMinWinRate}/>
                </Box>
                )}
                {props.memoArray.filterMaxFights>0 && (
                <Box className={"sliderRow"}>
                    {(props.gMode?.indexOf("g80")>=0 ) && (
                        <Tooltip title={" - "}><img src={"./attack_w.png"}/></Tooltip>
                    )}
                    {(props.gMode === 'd') && (
                        <Tooltip title={"  "}><img src={"./attack_d.png"}/></Tooltip>
                    )}
                    {(props.gMode === 'a' || props.gMode === undefined || props.gMode === null) && (
                    <Tooltip title={"  "}><img src={"./attack_w.png"}/></Tooltip>
                    )}
                    <ModernSlider min={props.memoArray.filterMinFights} max={props.memoArray.filterMaxFights}
                                  label={"some1"}
                                  initial={props.memoArray.minFights}
                                  callback={changeMinFights}/>
                </Box>
                )}
                <span>    <Button className={"svgButtonBig"} onClick={loadFilter}>
                    <UploadFileIcon/></Button> <Button className={"svgButtonBig"} onClick={saveFilter}><SimCardDownloadIcon/></Button></span>
                <HeroModernGrid additionalClasses={["heroIconTiny"]} highlightedHeroes={[]}
                                onClick={onHeroGridClick} selectedHeroes={[]} addedHeroes={heroFilterInclude}
                                removedHeroes={heroFilterExclude}/>
            </Box>
        </Modal>
    )
}